import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Card from "@/Card"
import Dice from "@/Dice"
import ValueMod from "@/effects/ValueMod"
import HighlightStage from "@/stages/HighlightStage"
import Deck from "@/Deck"
import EndStage from "@/stages/EndStage"

export default class BonusStage extends TextStage {
  private pickedCards: Card[] = []
  private leftToPick: number = 0
  public deck: Deck = new Deck([])

  constructor(game: Game) {
    super(game, "")
    this.id = "bonus"
  }

  onStart() {
    this._text = this.game.dialogue['bonusStage']
    this.deck = new Deck(this.game.deck.withoutFamily('Reward').draw(12))
    this.game.state.drawnCards = this.deck.cards

    this.game.round.addValue(1)
    this.game.state.cardsPickedThisRound = 0
    this.leftToPick = this.game.state.bonusRolls.value
    this.game.state.cardsToPick = this.leftToPick
    this.game.diceCount = 2
    this.game.diceSize = this.deck.size()
    this._allowDiceBuy = false
    this._allowNext = false

    if (this.leftToPick == 0) {
      this.onResume()
    }
  }

  onResume() {
    this.game.state.drawnCards = this.deck.cards
    this.game.diceCount = 2
    this.game.diceSize = this.deck.size()

    if (this.leftToPick == 0) {
      this.game.state.drawnCards = []
      this.game.diceCount = 0
      this._text = `Take a moment now, look at your cards.\n\nRead each one, if you still can read, that is.`
      this._nextStage = new EndStage(this.game)
      this._allowNext = true
      this.game.lockDice(true)
      //this.nextStage(new EndStage(this.game))
    }
  }

  onDiceRoll(values: number[]) {
    if (this.leftToPick > 0) {
      this.leftToPick -= 1
      this.pickedCards = this.deck.pick(values)
      this.game.lockDice(true)

      this.nextStage(new HighlightStage(this.game), { pickStage: this, highlightedCards: this.pickedCards, allowDiceBuy: false })
    }
  }
}
