import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Card from "@/Card"
import Dice from "@/Dice"
import ValueMod from "@/effects/ValueMod"

export default class DrawStage extends TextStage {
  private drawnCards: Card[] = []
  private cardsToPick: number = 0

  constructor(game: Game) {
    super(game, "")
  }

  onStart() {
    this._text = this.game.dialogue['drawStage']
    this.cardsToPick = 0
    this.drawnCards = this.game.deck.draw(8)
    this.game.diceSize = Math.min(6, this.drawnCards.length)
    this.game.state.drawnCards = this.drawnCards
    this._allowNext = false
    this.game.round.addValue(1)
    this.game.state.cardsPickedThisRound = 0
    this.game.state.cardsToPick = 0
    this.game.diceCount = 1
    this._allowDiceBuy = false
  }

  onDiceRoll(values: number[]) {
    if (this.allowNext()) return
    let value = Math.min(...values)
    this.cardsToPick = value
    this.game.state.cardsToPick = value
    this._text = `You will have to draw ${value} cards this round.`
    this._allowNext = true
    this.game.lockDice(true)
  }

  output(): any {
    return { drawnCards: this.drawnCards, cardsToPick: this.cardsToPick }
  }

  nextStage() {
    if (this.allowNext()) super.nextStage()
  }
}
