import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Dice from "@/Dice"
import Card from "@/Card"
import Deck from "@/Deck"
import IntensityStage from "@/stages/IntensityStage"
import HighlightStage from "@/stages/HighlightStage"
import BonusStage from "@/stages/BonusStage"

export default class PickStage extends TextStage {
  private pickedCards: Card[] = []
  private leftToPick: number = 0
  private diceCount: number = 1
  public deck: Deck = new Deck([])

  constructor(game: Game, text: string = "") {
    super(game, "")
  }

  onStart() {
    this._text = this.game.dialogue['pickStage']
    this.deck = new Deck(this.args.drawnCards)
    this.leftToPick = this.args.cardsToPick
    this.pickedCards = []

    this.diceCount = 1
    this.game.diceCount = this.diceCount
    this.game.diceSize = this.deck.size()
    this._allowNext = false
    this._allowDiceBuy = true
  }

  onResume() {
    this.game.state.drawnCards = this.deck.cards
    this.diceCount = Math.max(1, this.diceCount - 1)
    this.game.diceCount = this.diceCount
    this.game.diceSize = this.deck.size()
    this._allowDiceBuy = true
    if (this.leftToPick == 0) {
      this._text = `Good job! You completed round ${this.game.round.value}.`
      this.buttonLabel = "Next round"
      this._allowNext = true
      this.game.lockDice(true)

      if (this.game.round.fraction == 1) {
        if (this.game.state.bonusRolls.value > 0) {
          this.buttonLabel = "To Bonus Round"
        } else {
          this.buttonLabel = "Next"
        }
        this._nextStage = new BonusStage(this.game)
      }
    }
  }

  onDiceRoll(values: number[]) {
    if (this.leftToPick > 0) {
      this.leftToPick -= 1
      this.pickedCards = this.deck.pick(values)
      this.game.lockDice(true)
      this.diceCount = values.length

      this.nextStage(new HighlightStage(this.game), { pickStage: this, highlightedCards: this.pickedCards })
    }
  }
}
