import Hjson from "hjson"
import globalState from "@/store/globalState"
import Card from "@/Card"
import util from "@/util"

export default class Deck {
  public cards: Card[]

  constructor(cards: Card[] | undefined = undefined) {
    if (cards != undefined) {
      this.cards = cards
    } else {
      this.cards = this.parseCards()
    }
  }

  /** Returns a new deck with this one's cards, but with all cards of a given family removed. */
  withoutFamily(family: string): Deck {
    let cards: Card[] = []
    for (let card of this.cards) {
      if (card.family.name != family) cards.push(card)
    }
    return new Deck(cards)
  }

  /**
   * Returns the cards on 1-based indices specified by values.
   * @param values
   */
  pick(values: number[]) {
    let cards: Card[] = []
    for (let i = 0; i < this.cards.length; i++) {
      for (let value of values) {
        if (i == value - 1) cards.push(this.cards[i])
      }
    }
    return cards
  }

  draw(value: number): Card[] {
    let cards: Card[] = []
    for (let i = 0; i < value; i++) {
      let card: Card | undefined = this.cards.pop()
      if (card != undefined) cards.push(card)
      else break
    }
    return cards
  }

  /** Draws the card with given index. */
  drawIndex(index: number) {
    return this.cards.splice(index, 1)[0]
  }

  removeCard(card: Card) {
    let i = this.cards.findIndex(c => c.name == card.name && c.description == card.description)
    if (i != -1) this.drawIndex(i)
  }

  size(): number {
    return this.cards.length
  }

  shuffle() {
    this.cards = util.shuffle(this.cards)
  }

  get pickableCards(): Card[] {
    let cards: Card[] = []
    for (let card of this.cards) {
      if (!card.isParent) cards.push(card)
      for (let i = 0; i < card.levels.length; i++) {
        cards.push(card.getLevel(i))
      }
    }
    return cards
  }

  get allLevels(): Card[] {
    let cards: Card[] = []
    for (let card of this.cards) {
      cards.push(card)
      for (let i = 0; i < card.levels.length; i++) {
        cards.push(card.getLevel(i))
      }
    }
    return cards
  }

  private parseCards(): Card[] {
    let families: any = JSON.parse(globalState().cardFamilyString.value)
    let cards = []
    for (let family of families) {
      for (let card of family.cards) {
        let levels: Card[] = []
        if (card.levels != undefined) {
          for (let level of card.levels) {
            levels.push(new Card({
              family: family,
              thumb: (level.thumb) ? `cards/${level.thumb}` : (card.thumb) ? `cards/${card.thumb}` : `cards/${family.thumb}`,
              name: level.name,
              description: level.description,
              type: card.type,
              levels: [],
              parent: card,
              hue: family.hue,
              artist: (level.artist) ? level.artist : (card.artist) ? card.artist : (family.artist) ? family.artist : "Gardamuse"
            }))
          }
        }
        cards.push(new Card({
          family: family,
          thumb: (card.thumb) ? `cards/${card.thumb}` : `cards/${family.thumb}`,
          name: card.name,
          description: card.description,
          type: card.type,
          levels: levels,
          hue: family.hue,
          artist: (card.artist) ? card.artist : (family.artist) ? family.artist : "Gardamuse"
        }))
      }
    }

    return cards
  }
}
