import GameStage from "@/stages/GameStage"
import Game from "@/Game"
import TextStage from "@/stages/TextStage"
import Dice from "@/Dice"
import Card from "@/Card"
import Deck from "@/Deck"
import PickStage from "@/stages/PickStage"
import IntensityStage from "@/stages/IntensityStage"

export default class HighlightStage extends TextStage {
  private pickStage!: PickStage
  private highlightedCards: Card[] = []
  private done: boolean = false

  constructor(game: Game) {
    super(game, "")
  }

  onStart() {
    this.pickStage = this.args.pickStage
    this.highlightedCards = this.args.highlightedCards
    this._allowDiceBuy = (this.args.allowDiceBuy != undefined) ? this.args.allowDiceBuy : true
    if (this.highlightedCards.length == 1) {
      this._text = `Ah, you got ${this.highlightedCards[0].name}. Click on it to add it to your hand.`
    } else {
      this._text = `Because you rolled multiple dice you now get to choose one of the highlighted cards. Click on the one you want to add to your hand.`
    }

    for (let c of this.highlightedCards) {
      c.highlight = true
    }

    this.done = false
    this._allowNext = false
  }

  onCardClick(card: Card) {
    if (card.highlight) {
      for (let c of this.highlightedCards) {
        c.highlight = false
        c.scale = false
      }
      if (card.isParent) {
        this.pickStage.deck.removeCard(card)
        this.nextStage(new IntensityStage(this.game), { pickStage: this.pickStage, parentCard: card, allowDiceBuy: this._allowDiceBuy })
      } else {
        this.pickStage.deck.removeCard(card)
        this.game.pickCard(card)
        this.resumeStage(this.pickStage)
      }
    }
  }
}
