<template>
  <div class="milkfarm" :class="{'dialogue-only': game.showDialogueOnly, 'start-menu': game.stageId === 'stage1'}" v-if="isLoaded">
    <div class="cards-wrapper drawn-cards">
      <div class="cards" :class="{'tight': game.state.drawnCards.length > 8}">
        <template v-for="card in game.drawnCards" v-bind:key="card">
          <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="toggleScale(card); game.clickCard(card);"></PlayingCard>
        </template>
      </div>
    </div>
    <div class="window dialogue">
      <div class="text">
        {{ game.currentDialogue }}
      </div>
      <div class="round-counter">
        <h3 v-if="game.round.value > 0">{{ game.roundString }}</h3>
        <h1 v-else style="margin: 0; padding: 0;">Ditzy Dice</h1>
      </div>
      <div v-if="game.state.cardsToPick > 0" class="cards-to-pick">
        <h3>Picked {{ game.state.cardsPickedThisRound }} of {{ game.state.cardsToPick }} cards</h3>
      </div>
      <div v-if="game.round.value > 0 && game.stageId !== 'bonus'" class="bonus-rolls">
        <h3>Bonus rolls: {{ game.state.bonusRolls.value }} / {{ game.state.bonusRolls.max }}</h3>
      </div>
      <TextButton v-if="game.allowNextStage()" class="next-button" @click="game.nextStage()">
        <p style="margin: 0;">{{ game.buttonLabel }}</p>
      </TextButton>
    </div>
    <div class="cards-wrapper picked-cards">
      <div class="cards" :class="{'tight': game.state.pickedCards.length > 5}">
        <template v-for="card in game.state.pickedCards" v-bind:key="card">
          <PlayingCard :card="card" :class="{'scaled': card.scale}" @click="toggleScale(card)"></PlayingCard>
        </template>
      </div>
    </div>
    <div class="dices">
      <template v-for="die in game.state.dice" v-bind:key="die">
        <DiceView :dice="die"></DiceView>
      </template>
      <div class="dice-view add-dice" v-if="game.allowDiceBuy()" @click="game.buyDice()">
        <p>+</p>
      </div>
    </div>
    <div class="window credits">
      <h2 style="margin: 0;">Credits</h2>
      <PatreonButton></PatreonButton>
      <div>
        <p>Artist</p>
        <p>Artist</p>
        <p>Artist</p>
      </div>
    </div>
    <!--<keep-alive>
      <component :is="selectedTab" v-bind="currentProperties"></component>
    </keep-alive>-->
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Game from '@/Game'
import ValueMeter from '@/components/ValueMeter.vue'
import TextButton from '@/components/TextButton.vue'
import globalState from '@/store/globalState'
import SaveSystem from '@/SaveSystem'
import PlayingCard from "@/components/PlayingCard.vue"
import Card from '@/Card'
import DiceView from "@/components/DiceView.vue"
import PatreonButton from "@/components/PatreonButton.vue"

@Options({
  components: {
    ValueMeter, TextButton, PlayingCard, DiceView, PatreonButton
  },
  props: {
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    },
    toggleScale(card: Card) {
      card.scale = !card.scale
      if (this.scaledCard == card) {
        this.scaledCard.scale = false
        this.scaledCard = new Card({})
      } else {
        this.scaledCard.scale = false
      }
      if (card.scale) this.scaledCard = card
    }
  },
  computed: {
    game: function (): Game {
      return this._game
    }
  },
  async created() {
    await globalState().load()
    //this.saveSystem = new SaveSystem()
    this._game = new Game()
    this.isLoaded = true
  }
})
export default class GameView extends Vue {
  isLoaded = false
  //saveSystem!: SaveSystem
  //farm!: Farm
  _game!: Game
  scaledCard: Card = new Card({})
  tabs = [
    { id: 'AdminView', label: 'Office' },
    { id: 'StaffView', label: 'Pasture' },
    { id: 'ResearchView', label: 'Lab' }
  ]

  selectedTab: string = 'Menu'
}
</script>

