import Game from "@/Game"

export default class Dice {
  private _value: number = 1
  public max: number
  private _locked: boolean = false
  private isRolling: boolean = false
  private valueUsed: boolean = true // True when dice has been rolled but its value has not yet been used

  private game: Game

  // @ts-ignore
  constructor(max: number, game: Game = undefined) {
    this.max = max
    this.game = game
  }

  load(game: Game) {
    this.game = game
  }

  roll() {
    if (this.isRolling || this.locked) return
    this.isRolling = true
    this.rollingDice(10)
  }

  /** Returns the dice value and marks this dice as used until it is rolled again. */
  useValue(): number {
    this.valueUsed = true
    return this.value
  }

  get isUsed(): boolean {
    return this.valueUsed
  }

  private rollingDice(rollsLeft: number) {
    if (rollsLeft > 0) {
      this._value = rollsLeft
      setTimeout(() => {
        this.rollingDice(rollsLeft - 1)
      }, 50)
    } else {
      this._value = Math.floor(Math.random() * this.max) + 1
      this.valueUsed = false
      this.game.notifyDiceRoll(this.value)
      this.isRolling = false
    }
  }

  set locked(value: boolean) {
    this._locked = value
  }

  get locked(): boolean {
    return this._locked || !this.isUsed
  }

  get value(): number {
    return this._value
  }

  get rolling(): boolean {
    return this.isRolling
  }
}
