import Game from "@/Game"
import Card from "@/Card"

export default abstract class GameStage {
  public id: string = "stage"
  protected game: Game
  protected args: any = {}
  protected _nextStage: GameStage | undefined
  protected _allowNext: boolean = true
  protected _allowDiceBuy: boolean = false
  public buttonLabel: string = "Next"

  protected constructor(game: Game) {
    this.game = game
  }

  onStart() {}

  onResume() {}

  onDiceRoll(values: number[]) {}

  onCardClick(card: Card) {}

  /** Returns an arguments object for the next stage. */
  output(): any {}

  link(stage: GameStage): GameStage {
    this._nextStage = stage
    return stage
  }

  resumeStage(stage: GameStage) {
    stage.onResume()
    this.game.setStage(stage)
  }

  nextStage(stage: GameStage | undefined = undefined, args: any = undefined) {
    if (stage != undefined) {
      this.gotoNext(stage, args)
    } else if (this._nextStage != undefined) {
      this.gotoNext(this._nextStage, args)
    } else {
      this.game.setStage(this)
    }
  }

  allowNext(): boolean {
    return this._allowNext
  }

  allowDiceBuy(): boolean {
    return this._allowDiceBuy
  }

  private gotoNext(stage: GameStage, args: any = undefined) {
    stage.args = (args != undefined) ? args : this.output()
    stage.onStart()
    this.game.setStage(stage)
  }

  get text(): string {
    return ""
  }
}
