export default class Card {
  public readonly family: any
  public readonly thumb: string
  public readonly name: string
  public readonly description: string
  public readonly levels: Card[]
  public readonly type: string // numeric, select, single
  public readonly hue: number
  public readonly artist: string

  private readonly parent: Card

  public scale: boolean = false
  public highlight: boolean = false

  constructor(state: any) {
    this.family = state.family ? state.family : "None"

    this.thumb = state.thumb
    this.name = state.name
    this.description = state.description
    this.levels = state.levels ? state.levels : []
    this.type = state.type ? state.type : "single"
    this.parent = state.parent
    this.hue = state.hue ? state.hue : 0
    this.artist = state.artist ? state.artist : "Gardamuse"
  }

  get title(): string {
    if (this.hasParent) return `${this.parent.name}`
    return this.name
  }

  get subtitle(): string {
    if (this.hasParent) return `${this.name}`
    return ""
  }

  get level(): number {
    if (this.hasParent) {
      return this.parent.levels.findIndex((c: Card) => { return c.name == this.name }) + 1
    }
    return 0
  }

  get maxLevel(): number {
    if (this.hasParent) {
      return this.parent.levels.length
    }
    return this.levels.length
  }

  get levelString(): string {
    if (this.type == "single") return "1"
    if (this.isParent) return this.maxLevel.toFixed(0)
    return `${this.level} / ${this.maxLevel}`
  }

  getLevel(level: number): Card {
    if (this.type != "single" && level < this.levels.length) {
      return this.levels[level]
    }
    return this
  }

  get hasParent(): boolean {
    return this.parent != undefined
  }

  get isParent(): boolean {
    return this.type != "single" && this.levels.length > 0
  }
}
